import { createSlice     } from '@reduxjs/toolkit'


const roomSlice = createSlice({
     name: 'room',
     initialState: {
          rooms: {
               1: {
                    messages: [
                         { date: new Date('2022-08-20T08:01+03:00').getTime(), text: 'a      .   Reeinbit,        .', isMe: false },
                         { date: new Date('2022-08-20T08:02+03:00').getTime(), text: ',   .', isMe: true },
                         { date: new Date('2022-08-20T08:03+03:00').getTime(), text: '        ,    .   .', isMe: true },
                    ],
               },
               2: {
                    messages: [
                         { date: new Date('2022-08-21T20:01+03:00').getTime(), text: '!  ?', isMe: false },
                         { date: new Date('2022-08-21T20:02+03:00').getTime(), text: ' ?', isMe: false },
                         { date: new Date('2022-08-21T20:03+03:00').getTime(), text: '      Reenbit', isMe: true },
                    ],
               },
               3: {
                    messages: [
                         { date: new Date('2022-08-23T22:01+03:00').getTime(), text: '   ?', isMe: false },
                         { date: new Date('2022-08-23T22:02+03:00').getTime(), text: ',   )', isMe: true },
                    ],
               },
               4: {
                    messages: [
                         { date: new Date('2022-08-24T12:01+03:00').getTime(), text: ',      ?', isMe: false },
                         { date: new Date('2022-08-24T12:02+03:00').getTime(), text: ')', isMe: true },
                         { date: new Date('2022-08-24T12:03+03:00').getTime(), text: ',       .    - .', isMe: true },
                         { date: new Date('2022-08-24T12:04+03:00').getTime(), text: ',    )', isMe: false },
                    ],
               }
          },
          currentRoom: null,
     },
     reducers: {
          addMessage(state, action) {
               const [roomId, message] = action.payload;
               const existingRoom = state.rooms[roomId];
               if (existingRoom) {
                    existingRoom.messages = [...existingRoom.messages, message]
               }
          },
          addAnswer(state, action) {
               const [roomId, message] = action.payload;
               const existingRoom = state.rooms[roomId];
               if (existingRoom) {
                    existingRoom.messages = [...existingRoom.messages, message]
               }
          },
          setRoom(state, action) {
               state.currentRoom = action.payload;
          }
     }
})

export const { addMessage, addAnswer, setRoom } = roomSlice.actions;
export default roomSlice.reducer;